<template>
  <div>
    <header id="wx-header">
      <div class="center">
        <router-link to="/workbench" tag="div" class="iconfont icon-return-arrow">
          <span>返回</span>
        </router-link>
        <span>产品仓储</span>
      </div>
    </header>
    <van-row style="padding:10px 20px;border-bottom:1px solid #ccc;" @click="select">
      <van-col span="8">请选择库区库位</van-col>
      <van-col span="16">
        <div style="text-align:right;">{{area}}{{location}}</div>
      </van-col>
    </van-row>
    <transition name="fade">
      <div v-show="isShow" style="border:1px solid #ccc;padding:10px 0;background:white;">
        <van-row style="padding:10px 0 10px 20px;">
          <van-col span="3">库区：</van-col>
          <van-col span="21">
            <van-row>
              <van-radio-group v-model="radio1">
                <van-col span="8">
                  <van-radio name="1号库区">1号库区</van-radio>
                </van-col>
                <van-col span="8">
                  <van-radio name="2号库区">2号库区</van-radio>
                </van-col>
                <van-col span="8">
                  <van-radio name="3号库区">3号库区</van-radio>
                </van-col>
              </van-radio-group>
            </van-row>
          </van-col>
        </van-row>
        <van-row style="padding:10px 0 10px 20px;">
          <van-col span="3">库位：</van-col>
          <van-col span="21">
            <van-row>
              <van-radio-group v-model="radio2">
                <van-col span="8">
                  <van-radio name="1号库位">1号库位</van-radio>
                </van-col>
                <van-col span="8">
                  <van-radio name="2号库位">2号库位</van-radio>
                </van-col>
                <van-col span="8">
                  <van-radio name="3号库位">3号库位</van-radio>
                </van-col>
              </van-radio-group>
            </van-row>
          </van-col>
        </van-row>
        <div style="text-align:right;padding:0 20px;">
          <van-button plain type="info" size="small" @click="sure">确定</van-button>
          <van-button plain type="info" size="small" @click="cancel">取消</van-button>
        </div>
      </div>
    </transition>

    <!-- <van-row style="padding:10px 20px;">
      <van-checkbox-group v-model="result" @change="change">
        <van-col span="6">
          <van-checkbox name="a">待审核</van-checkbox>
        </van-col>
        <van-col span="6">
          <van-checkbox name="b">审核中</van-checkbox>
        </van-col>
        <van-col span="6">
          <van-checkbox name="c">通过</van-checkbox>
        </van-col>
      </van-checkbox-group>
    </van-row>-->
    <van-row style="padding:10px 20px;">
      <van-button type="primary" plain @click="checkAll" size="mini">全选</van-button>
      <van-button type="info" plain @click="toggleAll" size="mini">反选</van-button>
      <van-checkbox-group
        v-model="result"
        ref="checkboxGroup"
        @change="change"
        shape="square"
        style="margin-top:10px;"
      >
        <van-col span="6">
          <van-checkbox name="审核" shape="square">审核中</van-checkbox>
        </van-col>
        <van-col span="6">
          <van-checkbox name="通过" shape="square">通过</van-checkbox>
        </van-col>
        <van-col span="6">
          <van-checkbox name="不通过" shape="square">不通过</van-checkbox>
        </van-col>
      </van-checkbox-group>
    </van-row>
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <van-cell
        v-for="(item,index) in list"
        :key="index"
        :title="item.output+item.product"
        :value="item.status"
        is-link
        @click="$router.push({path:'/warehousing/details',query:{ id:item.id }})"
      />
    </van-list>
  </div>
</template>

<script>
export default {
  name: "list",
  data() {
    return {
      radio1: "",
      radio2: "",
      area: null, //库区
      location: null, //库位
      isShow: false,
      result: ["审核", "通过", "不通过"], //待审核、审核中、通过
      list: [],
      loading: false,
      finished: false
    };
  },
  activated() {
    console.log(asadd);
  },
  methods: {
    checkAll() {
      this.$refs.checkboxGroup.toggleAll(true);
      this.result = ["审核", "通过", "不通过"];
      //console.log(this.result);
    },
    toggleAll() {
      this.$refs.checkboxGroup.toggleAll();
      this.result = [];
      //console.log(this.result);
    },
    change(data) {
      //console.log(data);
      this.result = data;
      this.onLoad();
    },
    onLoad() {
      let params = {
        area: this.area,
        location: this.location,
        status:this.result.join(",")
      };
      this.$http
        .get("/api/warehousing/list", { params: params })
        .then(response => {
          let result = response.data;
          if (result && result.code == 200) {
            this.list = result.data;
          }
          this.loading = false;
          this.finished = true;
        });
    },
    select() {
      this.isShow = true;
    },
    sure() {
      this.isShow = false;
      this.area = this.radio1;
      this.location = this.radio2;
      this.onLoad();
    },
    cancel() {
      this.radio1 = "";
      this.radio2 = "";
    },
    getpage() {
      this.onLoad();
    }
  },
  watch: {
    $route: "getpage" //需要处理数据的方法
  }
};
</script>

<style>
.van-cell{
  line-height: 30px;
}
.van-field__control {
  text-align: right;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
</style>



